@charset "UTF-8";
/* Pizi React */
@import "../../node_modules/pizi-react/src/styles/main";
@import "../../node_modules/animate.css/animate.css";

@color-black-light: #444;
@color-main-light: #e7944b;
@color-main-medium-dark: #B25E18;
@color-main-dark: #301C14;
@color-hover: @color-black-light;

@border: 4px solid @color-black;
@border-light: 1px solid @color-black-light;
@border-radius: 14px;


@letter-spacing: 1.8px;

@breakpoint-xs: 500px;
@breakpoint-sm: 400px;

@app-max-width: 800px;

@header-height: 30px;
@footer-height: 30px;


a{
    color: LightSkyBlue;

    &:hover{
        color: LightBlue;
    }
}

@import "./app";
@import "./components/Footer/footer";
@import "./screens/Home/home";
@import "./screens/Login/login";
@import "./screens/RestUI/restui";
@import "./screens/RestUI/restui-detail";