.rest-ui{
    max-width: 600px;
    width: 100%;

    .collections{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        .pizi-input{
            align-self: center;

            li{
                color: LightSkyBlue;
            }
        }
    }

    h3{
        margin-bottom: 16px;
        color: @color-white;
    }

    ul{
        list-style: none;
        padding: 0;

        li{
            padding: 2px 0;
        }

        a{
            text-decoration: none;
        }

        .active a{
            color: peru;
        }
    }
}